import Utils from '../modules/utils'
import StatusView from './statusView'

const modelMakeMap = {
  PX013AN: 'Pace XG1v3',
  PX022AN: 'Pace XG2v2',
  PX001AN: 'Pace XG1v1',
  PXD01ANI: 'Pace XiD',
  PX032ANI: 'Pace Xi3v2',
  PX051AEI: 'Pace Xi5',
  AX061AEI: 'Arris Xi6',
  AX014AN: 'Arris XG1v4',
  AX013AN: 'Arris XG1v3',
  MX011AN: 'Arris XG1v1',
  SX022AN: 'Samsung XG2v2',
  CXD01ANI: 'Cisco XiD',
  CS011AN: 'Cisco G8',
  PR150BN: 'Pace RNG150',
  SR150BN: 'Samsung RNG150',
}

export default class DeviceStatus extends StatusView {
  static _rowsTemplate() {
    let list = [
      {
        key: 'Make',
        value: modelMakeMap[Object.keys(modelMakeMap).find(k => Utils.modelName.indexOf(k) !== -1)],
      },
      {
        key: 'Model',
        value: 'TODO',
        ref: 'Device.DeviceInfo.ModelName',
      },
      {
        key: 'Serial Number',
        value: 'TODO',
        ref: 'Device.DeviceInfo.SerialNumber',
      },
      {
        key: 'eSTB Mac (Device/Mgmt)',
        value: 'TODO',
        ref: 'Device.DeviceInfo.X_COMCAST-COM_STB_MAC',
      },
    ]
    if (Utils.isClientDevice) {
      if (Utils.isClientNoMocaDevice) {
        list = list.concat([
          {
            key: 'WiFi Mac',
            value: 'TODO',
            ref: 'Device.WiFi.SSID.1.MACAddress',
          },
          {
            key: 'Ethernet Mac',
            value: 'TODO',
            ref: 'Device.Ethernet.Interface.1.MACAddress',
          },
        ])
      } else {
        list = list.concat([
          {
            key: 'MoCA Mac',
            value: 'TODO',
            ref: 'Device.MoCA.Interface.1.MACAddress',
          },
        ])
      }
    }
    list = list.concat([
      {
        key: 'Receiver ID',
        value: Utils.placeHolderWrapText,
        ref: 'Device.X_COMCAST-COM_Xcalibur.Client.XRE.xreReceiverId',
        separator: true,
      },
      {
        key: 'HW Rev',
        value: 'TODO',
        ref: 'Device.DeviceInfo.HardwareVersion',
      },
      {
        key: 'Uptime',
        value: 'TODO',
        ref: 'Device.DeviceInfo.UpTime',
      },
      {
        key: 'Power Status',
        value: 'TODO',
        ref: 'Device.DeviceInfo.X_COMCAST-COM_PowerStatus',
      },
      {
        key: 'Boot Status',
        value: 'XRE connection established - successful',
        ref: 'Device.DeviceInfo.X_RDKCENTRAL-COM.BootStatus',
      },
    ])
    return list
  }
}
