import { Lightning } from 'wpe-lightning-sdk'

import TabView from './tabView'
import HScrollList from './hScrollList'
import VScrollList from './vScrollList'

export default class TabContainer extends Lightning.Component {
  static _template() {
    return {
      Header: {
        mountX: 0.5,
        x: w => w / 2,
        h: 55,
        flex: { direction: 'row' },
      },
      HScroll: {
        type: HScrollList,
        y: 65,
        w: w => w,
        h: h => h - 65,
      },
    }
  }

  _init() {
    this.index = 0
    this.numTabs = 0
    this.$setState('Navigation')
  }

  $setState(state) {
    this._setState(state)
  }

  addTab(name, type) {
    this.tag('Header').childList.a({
      ref: 'Title-' + this.numTabs,
      type: TabView,
      name: name,
      highlighted: !this.numTabs,
      flexItem: { marginLeft: 20, marginRight: 20 },
    })
    this.tag('HScroll').content.childList.a({
      ref: 'Tab-' + this.numTabs,
      type: type,
      w: this.tag('HScroll').finalW,
      h: h => h,
    })
    ++this.numTabs
  }

  static _states() {
    return [
      class Navigation extends this {
        $enter() {
          if (this.index < this.numTabs) this.tag(`Title-${this.index}`).highlighted = true
        }
        $exit() {
          if (this.index < this.numTabs) this.tag(`Title-${this.index}`).highlighted = false
        }
        _handleUp() {
          let tab = this.tag(`HScroll.Tab-${this.index}`)
          if (tab instanceof VScrollList) tab.up()
        }
        _handleDown() {
          let tab = this.tag(`HScroll.Tab-${this.index}`)
          if (tab instanceof VScrollList) tab.down()
          else this.$setState('TabFocused')
        }
        _handleLeft() {
          if (this.index > 0) {
            this.tag(`Title-${this.index}`).highlighted = false
            this.index = this.index - 1
            this.tag(`Title-${this.index}`).highlighted = true
            this.tag('HScroll').left()
          }
        }
        _handleRight() {
          if (this.index < this.numTabs - 1) {
            this.tag(`Title-${this.index}`).highlighted = false
            this.index = this.index + 1
            this.tag(`Title-${this.index}`).highlighted = true
            this.tag('HScroll').right()
          }
        }
      },
      class TabFocused extends this {
        _getFocused() {
          return this.tag(`HScroll.Tab-${this.index}`)
        }
      },
    ]
  }
}
