import { Lightning } from 'wpe-lightning-sdk'

import Device from './Device'

export default class DeviceList extends Lightning.Component {
  static _template() {
    return {
      y: 85,
      flex: { direction: 'column' }
    }
  }

  set items(items) {
    this.children = items.map((item, index) => {
      return {
        type: Device,
        y: index * 65,
        item
      }
    })

    this.fireAncestors('$checkForDevices')
    this._refocus()
  }

  remove() {
    this.childList.removeAt(this.index)
    this.children.forEach((device, i) => {
      device.patch({ y: 65 * i })
    })
    this.index = this.index === 0 ? 0 : this.index - 1
    this.fireAncestors('$checkForDevices')
  }

  _init() {
    this.index = 0
    this._setState('Devices')
  }

  get device() {
    return this.children[this.index].item
  }
  get item(){
    return this.children[this.index]
  }
  _handleEnter() {
    this.fireAncestors('$connectWifi',this.device)

  }

  _getFocused() {
    return this.children[this.index]
  }

  _handleUp() {
    if (this.index > 0) {
      this.index--
    } else {
      this.fireAncestors('$setState', 'Navigation')
    }
  }

  _handleDown() {
    if (this.index < this.children.length - 1) {
      this.index++
    }
  }
}
