import { Lightning } from 'wpe-lightning-sdk'
import Utils from './utils'

const separatorLine = 2 // TODO why 1 not visible
const keyFontSize = 12
const valueFontSize = 12
const valueMargin = 5

export default class DetailsItemRow extends Lightning.Component {
  static _template() {
    return {
      flex: { direction: 'row' },
      h: 25, // TODO: how remove this
      Item1: {
        flexItem: { grow: 2 },
        rect: true,
        Key: {
          mount: 0.5,
          x: w => w / 2,
          y: h => h / 2,
          text: {
            fontFace: 'Bold',
            fontSize: keyFontSize,
            textColor: Utils.charcoalGreyColor,
            textAlign: 'center',
          },
        },
      },
      Item2: {
        rect: true,
        w: separatorLine,
      },
      Item3: {
        flexItem: { grow: 3 },
        rect: true,
        Value: {
          mountY: 0.5,
          x: valueMargin,
          y: h => h / 2,
          text: {
            fontFace: 'Regular',
            fontSize: valueFontSize,
            textColor: Utils.blackSolidColor,
            wordWrap: true,
          },
        },
      },
    }
  }

  set key(text) {
    this.tag('Key').text = text
  }

  set value(text) {
    this.tag('Value').text = text
  }

  set odd(odd) {
    this.tag('Item1').color = this.tag('Item3').color = odd ? 0xffe8e8e8 : 0xffd1d1d1
    this.tag('Item2').color = odd ? 0xffd1d1d1 : 0xffe8e8e8
  }
}
