import { Lightning } from 'wpe-lightning-sdk'

import screen from '../../../screen'
import BT from '../../../modules/BT'

export default class Titles extends Lightning.Component {
  static _template() {
    return {
      Container: {
        y: screen.h - 300,
        x: screen.w / 2 + 15,
        mountX: 0.5,
        flex: { direction: 'column', alignItems: 'center' },
        SongTitle: {
          text: { fontFace: 'Default', fontSize: 30 }
        },
        ArtistAndAlbum: {
          text: { fontFace: 'Default', fontSize: 30 }
        }
      }
    }
  }

  _active() {
    const song = BT.getPaired()
    this.tag('SongTitle').patch({ text: { text: song.title } })
    this.tag('ArtistAndAlbum').patch({ text: { text: `${song.artist} - ${song.album}` } })
  }
}
