import { Lightning } from 'wpe-lightning-sdk'

const scrollDuration = 0.2

export default class HScrollList extends Lightning.Component {
  static _template() {
    return {
      clipping: true,
      Content: {
        flex: { direction: 'row' },
        h: h => h,
      },
    }
  }

  _init() {
    this.index = 0
  }

  get content() {
    return this.tag('Content')
  }

  left() {
    if (this.index > 0) {
      --this.index
      this._scroll()
    }
  }

  right() {
    if (this.index < this.content.childList.length - 1) {
      ++this.index
      this._scroll()
    }
  }

  _scroll() {
    this.content
      .animation({
        duration: scrollDuration,
        actions: [
          {
            p: 'x',
            v: {
              0: this.content.x,
              1: -this.content.childList.getAt(this.index).finalX,
            },
          },
        ],
      })
      .start()
  }
}
