import { Lightning } from 'wpe-lightning-sdk'

import Header from './Header'
import Titles from './Player/Titles'
import Seekbar from './Player/Seekbar'
import Controls from './Player/Controls'
import AlbumArt from './Player/AlbumArt'
import Equalizer from './Player/Equalizer'

export default class PlayerScreen extends Lightning.Component {
  static _template() {
    return {
      y: -160,
      Header: { type: Header, title: 'Bluetooth Audio In / Player' },
      Equalizer: { type: Equalizer },
      AlbumArt: { type: AlbumArt },
      Titles: { type: Titles },
      Seekbar: { type: Seekbar },
      Controls: { type: Controls }
    }
  }

  _active() {
    this.fireAncestors('$hideNav')
  }

  _getFocused() {
    return this.tag('Controls')
  }
}
