import Utils from '../modules/utils'
import VScrollList from '../modules/vScrollList'
import NetworkConnection from './networkConnection'
import WIFIStatus from './wifiStatus'
import DeviceLocalStatus from './deviceLocalStatus'
import DocsisStatus from './docsisStatus'
import DocsisLevelsStatus from './docsisLevelsStatus'
import InBandNetworkStatus from './inBandNetworkStatus'
import ErrorsNetworkStatus from './errorsNetworkStatus'
import FlowsInUseStatus from './flowsInUseStatus'
import DsgStatus from './dsgStatus'
import TunnelInfoScreenStatus from './tunnelInfoScreenStatus'
import McardCahnStatus from './mcardCahnStatus'
import CanhStatus2 from './canhStatus2'
import CanhStatus3 from './canhStatus3'
import CanhStatus4 from './canhStatus4'
import CanhStatus5 from './canhStatus5'
import CanhStatus6 from './canhStatus6'
import OperationalStatus from './operationalStatus'

const gridLine = 2 // TODO why 1 not visible

export default class NetworkConnectionsView extends VScrollList {
  static _template() {
    const t = {
      clipping: true,
      Content: {
        flex: { direction: 'column', padding: gridLine },
        w: w => w - 2 * gridLine,
        rect: true,
        color: Utils.blackSolidColor,
        Row1: {
          w: w => w - 2 * gridLine,
          flex: { direction: 'row' },
          Item1: {
            w: w => (w - gridLine) / 2,
            type: DeviceLocalStatus,
            title: 'Device Local',
          },
          Item2: {
            flexItem: { marginLeft: gridLine },
            w: w => (w - gridLine) / 2,
            type: NetworkConnection,
            title: 'Network Connection',
          },
        },
      },
    }
    if (Utils.isClientNoMocaDevice) {
      t.Content.Row2 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item3: {
          w: w => w,
          type: WIFIStatus,
          title: 'WIFI Stats',
        },
      }
    }
    if (!Utils.isClientDevice) {
      t.Content.Row3 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item4: {
          w: w => (w - gridLine) / 2,
          type: DocsisStatus,
          title: 'DOCSIS',
        },
        Item5: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: DocsisLevelsStatus,
          title: 'DOCSIS Levels',
        },
      }
      t.Content.Row4 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item6: {
          w: w => (w - gridLine) / 2,
          type: InBandNetworkStatus,
          title: 'In-Band Network',
        },
        Item7: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: ErrorsNetworkStatus,
          title: 'Errors',
        },
      }
      t.Content.Row5 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item8: {
          w: w => (w - gridLine) / 2,
          type: FlowsInUseStatus,
          title: 'Flows in Use',
        },
        Item9: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: DsgStatus,
          title: 'DSG',
        },
      }
      t.Content.Row6 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item10: {
          w: w => (w - gridLine) / 2,
          type: TunnelInfoScreenStatus,
          title: 'Tunnel Info Screen 1',
        },
        Item11: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: McardCahnStatus,
          title: 'MCARD CAHN Status 1',
        },
      }
      t.Content.Row7 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item12: {
          w: w => (w - gridLine) / 2,
          type: CanhStatus2,
          title: 'CANH Status 2',
        },
        Item13: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: CanhStatus3,
          title: 'CANH Status 3',
        },
      }
      t.Content.Row8 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item14: {
          w: w => (w - gridLine) / 2,
          type: CanhStatus4,
          title: 'CANH Status 4',
        },
        Item15: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: CanhStatus5,
          title: 'CANH Status 5',
        },
      }
      t.Content.Row9 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item16: {
          w: w => (w - gridLine) / 2,
          type: CanhStatus6,
          title: 'CANH Status 6',
        },
        Item17: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: OperationalStatus,
          title: 'Operational Status',
        },
      }
    }
    return t
  }

  _init() {
    super._init()
    this.refresh()
  }

  refresh() {
    for (let i = 1; i <= 17; i++) {
      if (this.tag(`Item${i}`)) this.tag(`Item${i}`).refresh()
    }
  }
}
