import DetailsItem from '../modules/detailsItem'

const tr69Url = 'http://127.0.0.1:10999'
const tr69NameRegex = /\w+\.\w+/

export default class StatusView extends DetailsItem {
  refresh() {
    let rows = this.rows
    if (rows.length) {
      this._loadData(
        rows.map(o => o.ref),
        this.stage.root.getOption('debug')
      ).then(
        map => {
          rows.forEach(o => {
            if (o.ref && o.ref in map) o.value = map[o.ref]
          })
        },
        e => console.log('Err', e)
      )
    }
  }

  _loadData(list, debug = false) {
    return new Promise((resolve, reject) => {
      list = list.filter(s => tr69NameRegex.test(s))
      let result = {}
      if (!list.length) resolve(result)
      else {
        const xhr = new XMLHttpRequest()
        xhr.open('POST', tr69Url, true)
        xhr.setRequestHeader('Content-Type', 'application/json')
        xhr.onerror = reject
        xhr.onreadystatechange = () => {
          if (xhr.readyState === XMLHttpRequest.DONE) {
            if (xhr.status === 200) {
              JSON.parse(xhr.responseText).paramList.forEach(o => (result[o.name] = o.value))
              if (debug) console.log(`RESP: ${JSON.stringify(result)}`)
              resolve(result)
            } else {
              reject(xhr.status)
            }
          }
        }
        let body = { paramList: list.map(x => ({ name: x })) }
        if (debug) console.log(`REQ: ${JSON.stringify(body)}`)
        xhr.send(JSON.stringify(body))
      }
    })
  }
}
