import { Lightning } from 'wpe-lightning-sdk'
import Utils from './utils'
import DetailsItemRow from './detailsItemRow'

const lineMargin = 20
const titleFontSize = 18

export default class DetailsItem extends Lightning.Component {
  static _template() {
    let t = {
      flex: { direction: 'column' },
      rect: true,
      color: Utils.greyColor,
      Row0: {
        flex: { direction: 'row' },
        Header: {
          flexItem: { grow: 1 },
          h: 35,
          rect: true,
          color: Utils.xfinityBlueColor,
          Title: {
            mount: 0.5,
            x: w => w / 2,
            y: h => h / 2,
            text: {
              fontFace: 'Bold',
              fontSize: titleFontSize,
              textColor: Utils.charcoalGreyColor,
            },
          },
        },
      },
      Columns: {
        flex: { direction: 'row' },
        flexItem: { margin: lineMargin },
        w: w => w - 2 * lineMargin,
        Left: {
          flex: { direction: 'column' },
          w: w => w,
        },
        Right: {
          flex: { direction: 'column' },
          w: 0,
        },
      },
    }
    let rt = this._rowsTemplate()
    rt.forEach((v, i) => (v.odd = i % 2))
    rt.forEach(v => (v.type = DetailsItemRow))
    rt.forEach(v => (v.flexItem = { marginTop: v.separator ? lineMargin : 0 }))
    rt.forEach((v, i) => {
      if (v.left || v.right) {
        t.Columns.Left.w = w => (w - lineMargin) / 2
        t.Columns.Right.w = w => (w - lineMargin) / 2
        t.Columns.Right.flexItem = { marginLeft: lineMargin }
      }
      t.Columns[v.right ? 'Right' : 'Left'][`Row${i}`] = v
    })
    return t
  }

  static _rowsTemplate() {
    return []
  }

  get rows() {
    return this.tag('Left').children.concat(this.tag('Right').children)
  }

  get title() {
    return this.tag('Title').text
  }

  set title(title) {
    this.tag('Title').text = title
  }
}
