import { Lightning } from 'wpe-lightning-sdk'

import theme from '../Bluetooth/theme'
import Devices from './Devices'
import Refresh from './Refresh'

export default class WiFiSettings extends Lightning.Component {
  static _template() {
    return {
      Title: {
        x: 50,
        y: 60,
        color: theme.primary,
        text: {
          fontFace: 'Default',
          fontSize: 36,
          text: 'XFinity WiFi Settings'
        }
      },
      Devices: { type: Devices },
      Refresh: { type: Refresh }
    }
  }

  _init() {
    this._setState('Devices')
  }

  static _states() {
    return [
      class Devices extends this {
        _getFocused() {
          return this.tag('Devices')
        }

        _handleRight() {
          this._setState('Refresh')
        }
      },
      class Refresh extends this {
        _getFocused() {
          return this.tag('Refresh')
        }

        _handleLeft() {
          this._setState('Devices')
        }
        _handleEnter() {
          this.tag('Devices').refresh()
        }
      }
    ]
  }
}
