import AvStatus from './avStatus'
import HDMIStatus from './hdmiStatus'
import VODStatus from './vodStatus'
import Utils from '../modules/utils'
import VScrollList from '../modules/vScrollList'

const gridLine = 2 // TODO why 1 not visible

export default class AvView extends VScrollList {
  static _template() {
    return {
      clipping: true,
      Content: {
        flex: { direction: 'column', padding: gridLine },
        w: w => w - 2 * gridLine,
        rect: true,
        color: Utils.blackSolidColor,
        Item1: {
          w: w => w - 2 * gridLine,
          type: HDMIStatus,
          title: 'HDMI',
        },
        Item2: {
          flexItem: { marginTop: gridLine },
          w: w => w - 2 * gridLine,
          type: AvStatus,
          title: 'A/V',
        },
        Item3: {
          flexItem: { marginTop: gridLine },
          w: w => w - 2 * gridLine,
          type: VODStatus,
          title: 'VOD',
        },
      },
    }
  }

  _init() {
    super._init()
    this.refresh()
  }

  refresh() {
    for (let i = 1; i <= 3; i++) {
      if (this.tag(`Item${i}`)) this.tag(`Item${i}`).refresh()
    }
  }
}
