import { Lightning } from 'wpe-lightning-sdk'
import Utils from './utils'

export default class TabView extends Lightning.Component {
  static _template() {
    return {
      h: 55,
      text: {
        fontFace: 'Regular',
        fontSize: 25,
        textColor: Utils.textColor,
      },
      BotSeparator: {
        rect: true,
        w: w => w,
        y: y => y - 1,
        h: 1,
        color: Utils.greyColor,
      },
      Line: {
        rect: true,
        w: w => w,
        y: y => y - 5,
        h: 5,
        color: Utils.xfinityBlueColor,
      },
    }
  }

  get name() {
    return this.text
  }

  set name(name) {
    this.text = name
  }

  get highlighted() {
    return this.tag('Line').visible
  }

  set highlighted(highlighted) {
    this.tag('Line').visible = highlighted
  }
}
