import { Lightning } from 'wpe-lightning-sdk'

const scrollDuration = 0.2

export default class VScrollList extends Lightning.Component {
  static _template() {
    return {
      clipping: true,
      Content: {
        flex: { direction: 'column', padding: 0 },
        w: w => w,
      },
    }
  }

  _init() {
    this.index = 0
  }

  get content() {
    return this.tag('Content')
  }

  up() {
    if (this.index > 0) {
      --this.index
      this._scroll()
    }
  }

  down() {
    if (this.index < this.content.childList.length - 1) {
      ++this.index
      this._scroll()
    }
  }

  _scroll() {
    this.content
      .animation({
        duration: scrollDuration,
        actions: [
          {
            p: 'y',
            v: {
              0: this.content.y,
              1: -this.content.childList.getAt(this.index).finalY + this.content.flex.padding,
            },
          },
        ],
      })
      .start()
  }
}
