import { Lightning } from 'wpe-lightning-sdk'
import Utils from './utils'

export default class Toast extends Lightning.Component {
  static _template() {
    return {
      mount: 0.5,
      x: 960,
      y: 540,
      w: 300,
      h: 100,
      rect: true,
      pivot: 0.5,
      color: Utils.blackSolidColor,
    }
  }

  show() {
    this.animation({
      duration: 0.3,
      actions: [
        {
          p: 'alpha',
          v: {
            0: 0,
            1: 1,
          },
        },
        {
          p: 'scale',
          v: {
            0: 0,
            1: 1,
          },
        },
      ],
    }).start()
  }
}
