import Utils from '../modules/utils'

import DeviceStatus from './deviceStatus'
import NetworkConnection from './networkConnection'
import Firmware from './firmwareStatus'
import XRE from './xreStatus'
import TunerStatus from './tunerStatus'
import Memory from './memoryStatus'
import InitHistory from './initHistoryStatus'
import HostTru2Way from './hostTru2WayStatus'
import HostProperties from './hostPropertiesStatus'
import CableCardStatus from './cableCardStatus'
import MCardCertificateStatus from './mCardCertificateStatus'
import WIFIStatus from './wifiStatus'
import VScrollList from '../modules/vScrollList'

const gridLine = 2 // TODO why 1 not visible

export default class InstallSummaryView extends VScrollList {
  static _template() {
    const t = {
      clipping: true,
      Content: {
        flex: { direction: 'column', padding: gridLine },
        w: w => w - 2 * gridLine,
        rect: true,
        color: Utils.blackSolidColor,
        Row1: {
          w: w => w - 2 * gridLine,
          flex: { direction: 'row' },
          Item1: {
            w: w => (w - gridLine) / 2,
            type: DeviceStatus,
            title: 'Device Status',
          },
          Item2: {
            flexItem: { marginLeft: gridLine },
            w: w => (w - gridLine) / 2,
            type: NetworkConnection,
            title: 'Network Connection',
          },
        },
        Row2: {
          w: w => w - 2 * gridLine,
          flex: { direction: 'row' },
          flexItem: { marginTop: gridLine },
          Item3: {
            w: w => (w - gridLine) / 2,
            type: Firmware,
            title: 'Firmware',
          },
          Item4: {
            flexItem: { marginLeft: gridLine },
            w: w => (w - gridLine) / 2,
            type: XRE,
            title: 'XRE',
          },
        },
        Row3: {
          w: w => w - 2 * gridLine,
          flex: { direction: 'row' },
          flexItem: { marginTop: gridLine },
          Item5: {
            w: w => (w - gridLine) / 2,
            type: TunerStatus,
            title: 'Tuner Status',
          },
          Item6: {
            flexItem: { marginLeft: gridLine },
            w: w => (w - gridLine) / 2,
            type: Memory,
            title: 'Memory',
          },
        },
      },
    }
    if (Utils.isClientNoMocaDevice) {
      t.Content.Row4 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item7: {
          w: w => w,
          type: WIFIStatus,
          title: 'WIFI Stats',
        },
      }
    }
    if (!Utils.isClientDevice) {
      t.Content.Row5 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item8: {
          w: w => (w - gridLine) / 2,
          type: InitHistory,
          title: 'Init History',
        },
        Item9: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: HostTru2Way,
          title: 'Host: Tru2Way',
        },
      }
      t.Content.Row6 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item10: {
          w: w => (w - gridLine) / 2,
          type: HostProperties,
          title: 'Host: Properties',
        },
        Item11: {
          flexItem: { marginLeft: gridLine },
          w: w => (w - gridLine) / 2,
          type: CableCardStatus,
          title: 'Cable Card',
        },
      }
      t.Content.Row7 = {
        w: w => w - 2 * gridLine,
        flex: { direction: 'row' },
        flexItem: { marginTop: gridLine },
        Item12: {
          w: w => w,
          type: MCardCertificateStatus,
          title: 'M-Card Certificate',
        },
      }
    }
    return t
  }

  _init() {
    super._init()
    this.refresh()
  }

  refresh() {
    for (let i = 1; i <= 12; i++) {
      if (this.tag(`Item${i}`)) this.tag(`Item${i}`).refresh()
    }
  }
}
